.home__hero-section {
  color: #fff;
  padding: 75px 0 250px 0;
}

.home__hero-section-end {
  padding-bottom: 175px;
}

.home__hero-section-gif {
  color: #fff;
  padding: 50px 0 250px 0;
}

.home__hero-row {
  align-items: center;
}

.row {
  display: flex;
  margin-right: -15px;
  margin-bottom: -15px;
  margin-left: -15px;
  flex-wrap: wrap;
  align-content: stretch;
}

.col {
  margin-bottom: 15px;
  padding-right: 15px;
  padding-left: 15px;
  flex: 1;
  max-width: 50%;
  flex-basis: 50%;
}

.home__hero-text-wrapper {
  max-width: 540px;
  padding-top: 0;
  padding-bottom: 60px;
}

.top-line {
  color: #c6bfff;
  font-size: 16px;
  line-height: 16px;
  font-weight: 700;
  letter-spacing: 1.4px;
  text-transform: uppercase;
  margin-bottom: 16px;
  padding-left: 30px;
}

.top-line-reverse {
  padding-left: 40px;
  padding-right: 20px;
  margin-left: -100px;
}

.heading {
  margin-bottom: 24px;
  margin-right: -40px;
  font-size: 48px;
  line-height: 1.1;
  font-weight: 600;
  color: #f7f8fa;
  text-align: left;
  padding-left: 30px;
}

.heading-reverse {
  padding-left: 40px;
  padding-right: 80px;
  margin-left: -100px;
}

.heading-alt {
  color: #151c2a;
  font-size: 80px;
}

.lightBg {
  background-color: #fff;
}

.aboutmeBg {
  background-color: #e7e0da; 
}

.dark {
  color: #151c2a;
}

.darkBg {
  background-color: #151c2a;
}

.home__hero-subtitle {
  white-space: pre-line;
  max-width: 600px;
  margin-right: -40px;
  margin-bottom: 35px;
  font-size: 18px;
  line-height: 24px;
  padding-left: 30px;
}

.home__hero-subtitle-reverse {
  padding-left: 40px;
  padding-right: 80px;
  margin-left: -100px;
}

.home__hero-img-wrapper {
  max-width: 500px;
}

.home__hero-img {
  max-width: 95%;
  padding-right: 0;
  margin: 0 0 0 50px;
  display: block;
}

.home__hero-gif {
  max-width: 95%;
  max-height: 500px;
  padding-right: 0;
  margin: auto;
  display: block;
}

img {
  border: 0;
  max-width: 100%;
  vertical-align: middle;
  display: inline-block;
}

.container {
  height: 300px;
  padding-right: 100px;
  padding-left: 100px;
}

.project-section-div {
  padding-bottom: 50px;
}

@media screen and (min-width: 560px) and (max-width: 900px) {
  .home__hero-section-project {
    padding: 75px 0 600px 0;
  } 

  .home__hero-section-end {
    padding: 400px 0 350px 0;
  }
  
  .heading-alt {
     margin-left: -30px;
   }
   
   .heading-project {
     margin-left: 0px;
   }

   .heading-reverse {
     margin-left: -20px;
     padding-left: 40px;
     padding-right: 10px;
   }

   .home__hero-subtitle-alt {
     margin-left: -30px;
   }

   .btn {
     margin-left: 0px;
   }

   .home__hero-img-project {
    margin-left: 0px;
    margin-top: -40px;
    padding-left: 100px;
   }

   .home__hero-subtitle-project {
     margin-right: 0px;
     margin-left: 0px;
     margin-bottom: 0px;
   }

   .home__hero-subtitle-reverse {
     margin-left: -20px;
     padding-right: 10px;
   }

   .project-col {
    max-width: 100%;
    flex-basis: 100%;
  }

  .top-line-reverse {
    margin-left: -20px;
    margin-top: 50px;
  }
}

